.hero{
    display: flex;
    justify-content: space-between;
}
.hero-b{
    width: 22rem;
    height: 30rem;
    left: 0;
}

.left-h{
    padding: 4rem;
    padding-top: 1.5rem;
    flex:3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

.right-h{
    flex:1;
    position: relative;
    background: var(--orange);
}
.the-best-ad{
margin-top: 4rem;
background: #363d42;
border-radius: 4rem;
width: fit-content;
padding: 1rem 1.3rem ;
text-transform: uppercase;
color: white;
position: relative;
display: flex;
align-items: center;
justify-content: flex-start;
}

.the-best-ad>div{
position: absolute;
background:var(--orange);
width: 5.4rem;
height: 80%;
left: .5rem;
border-radius: 3rem;
}

.the-best-ad>span{
  z-index:1;
  /* margin-top: -5rem; */
}

.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-transform: uppercase;
    font-size: 4.2rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text  div:nth-of-type(3){
    font-size: 1rem;
    font-weight: normal;
    text-transform: none;
    letter-spacing: 1px;
}

.figures{
    display: flex;
    gap: 1.5rem;
}

.figures>div{
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figures>div>span:nth-of-type(2){
    color: var(--gray);
text-transform: uppercase;
}

.hero-buttons{
    display: flex;
    gap: 1rem;
    font-size: normal;
}

.hero-buttons>:nth-of-type(1){
    color: white;
    background: var(--orange);
    width: 8rem;
}

.hero-buttons>:nth-of-type(2){
    color: white;
    background: transparent;
    width: 8rem;
    border: 2px solid var(--orange);
}

.right-h>.btn{
    position: absolute;
    right: 3rem;
    top: 2rem;
    color: black;
}

.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--darkGrey);
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: .5rem;
    position: absolute;
    right: 6rem;
    top: 6rem;
}
.heart-rate>img{
    width: 2rem;
}

.heart-rate>:nth-child(2){
    color: var(--gray);
    
}
.heart-rate>:nth-child(3){
    color:white;
    font-size: 1.4rem;

}

.hero_img{
    position: absolute;
    top: 10rem;
    right: 8rem;
    width: 26rem;
    z-index: 4;
    /* border: 2px solid red; */
}
.hero_bg{
    position: absolute;
    top: 4rem;
    right: 22rem;
    z-index: 1;
    width: 16rem;
}
.calories{
    display: flex;
    gap: 2rem;
    background: var(--caloryCard);
    border-radius: .5rem;
    padding: 1rem;
    width: 16rem;
    position: absolute;
    top: 34rem;
    right: 32rem;
}
.calories>img{
    height: 5rem;
}
.calories>div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calories>div>:nth-child(1){
    color:var(--gray);
    font-weight: 500;
}
.calories>div>:nth-child(2){
    color: white;
    font-size: 1.5rem;
}