.join{
display: flex;
padding: 0 2rem;
gap:10rem;
}

.left-j{
    color: white;
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    position: relative;
}
.left-j>hr{
    position: absolute;
    width: 10.5rem;
    border: 4px solid orange;
    background:orange;
    /* border-radius: 20%; */
    top: -1.5rem;
    border-radius: 1rem;
}

.right-j{
    display: flex;
    justify-content: center;
    align-items: center;
}
.email-container{
    display: flex;
    gap: 2rem;
    background-color: gray;
    padding: 1rem 2rem;
}
.email-container>input{
    background: transparent;
    border: none;
    outline: none;
    color: lightgray;
}
::placeholder{
    color: var(--gray);
}
.btn-j{
    background: orange;
    color: white;
    outline: none;
    border: none;
}