:root{
    --lightgray: #D9D9D9;
    --gray: #9c9c9c;
    --orange: #f48915;
    --darkGrey: #464D53;
    --caloryCard: #656565;
    --planCard:background-image: linear-gradient(to right top, #f13e00, #f15704, #f16b10, #f17c1e, #f18c2c, #f38f35, #f4923e, #f69546, #f98d4c, #fa8652, #fb7e58, #fb775f);
    --appColor: #3c3f45;

}

body{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}
.App{
    /* background-image: var(--orange); */
    /* mix-blend-mode: overlay; */
    background: var(--appColor);
    display: flex;
    flex-direction: column;
    gap: 6rem;
    overflow: hidden;
}

::-webkit-scrollbar{
    display: none;
}

.stroke-text{
    color: transparent;
    font-family: Arial, Helvetica, sans-serif;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;

}

.btn{
    background: white;
    padding: .5rem;
    border: 4px solid  transparent;
    transition: 300ms all;
}

.btn:hover{
    cursor: pointer ;
}
.blur{
background: orange;
position: absolute;
border-radius: 50%;
filter: blur(212px);
z-index: -9;
}