.programmes{
    display: flex;
    gap: 2rem;
    flex-direction: column;
    padding: 0 2rem;
}
.programmes-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.right-h>.programmes-category{
    display: flex;
    flex-direction: column;
}

.programmes-category{
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
}

.category{
    display: flex;
    flex-direction: column;
    background: grey;
    gap: 2rem;
    padding: 2rem;
    color: white;
    justify-content: space-between;
    transition: all .3s;
    overflow: hidden;
}

.category>:nth-child(1){
    width: 2rem;
    height: 2rem;
    fill: white;
}
.category>:nth-child(2){
   font-size: 1.3rem;
   font-weight: bold;
   letter-spacing: .1rem;
}
.category>:nth-child(3){
   font-size: 1rem;
   line-height: 1.9rem;
}

.join-now{
    display: flex;
    align-items: center;
    gap: 2rem;
    border: 2px solid white;
    border-radius: .3rem;
    overflow: hidden;
    width: fit-content;
    padding: 0.5rem;
    text-align: center;
}
.join-now>img{
width: 1rem;
}
.category:hover{
    background-image: linear-gradient(to right top, #f13e00, #f15704, #f16b10, #f17c1e, #f18c2c, #f38f35, #f4923e, #f69546, #f98d4c, #fa8652, #fb7e58, #fb775f);
 transform: scale(1.1);

 
}