.reason{
    padding: 0 2rem;
    display: flex;
   gap: 2rem;
}

.left-reason{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}
.left-reason>img{
    object-fit: cover;
}
.left-reason>:nth-child(1){
    width: 14rem;
    grid-row: 1/3;
    height: 28.2rem;
}
.left-reason>:nth-child(2){
 width: auto;
 height: 16rem;
 grid-column: 2/4;
}
.left-reason>:nth-child(3){
 width: 14rem;
 grid-row: 2;
 grid-column: 2/3;
}
.left-reason>:nth-child(4){
 width: 10rem;
 grid-row: 2;
 grid-column: 3/4;
 height: 11.4rem;
}
.right-reason{
    flex: 1 1;
    text-transform: uppercase;
    gap:1rem;
    display: flex;
    flex-direction: column;
}

.right-reason>span{
    font-weight: bold;
    color: var(--orange);

}

.right-reason>div{
    color: white;
    font-size: 3rem;
    font-weight: bold;
}

.right-reason>.details-r{
 display: flex;
 flex-direction: column;
 gap: 1rem;
}

.right-reason>.details-r>div{
    display: flex;
    font-size: 1rem;
    gap: 1rem;
    align-items: center;
}

.details-r>div>img{
    width: 2rem;
    height: 2rem;
}
.details-r .our-p{
color: var(--gray);
font-weight: normal;
font-size: 1.4rem;
}

.partners{
    display: flex;
    gap: 1rem;
}
.partners>img{
    width: 2.5rem;
}