.header{
    display: flex;
    justify-content: space-between;
    gap: 20rem;
}

.header .logo{
width: 10rem;
height: 3rem;

}

.header-menu{
    list-style: none;
    display: flex;
    gap: 1.5rem;

}

.header-menu li {
    /* margin-top: .5rem; */

 
}

.header-menu li a{
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    transition: .3s;
    
}
.header-menu li a:hover{
    color: var(--orange);
      cursor: pointer;
  }