.plans-container{
    margin-top: -4rem;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
}
.plans{
    display: flex;
    justify-content: center;
    gap: 3rem;
}
.plan{
    display: flex;
    flex-direction: column;
    background: var(--caloryCard);
    gap: 2rem;
    color: white;
    padding: 1.5rem;
    width: 20rem;
   margin-top: 5rem;
}
.plan:nth-child(2){
    background-image: linear-gradient(to right top, #f13e00, #f15704, #f16b10, #f17c1e, #f18c2c, #f38f35, #f4923e, #f69546, #f98d4c, #fa8652, #fb7e58, #fb775f);
    transform: scale(1.1);
}
.plan:nth-child(2)>.btn:hover{
    border: 4px solid white;
    color: white;

}
.plan>svg{
    fill: var(--orange);
    width: 2rem;
    height: 2rem;
}
.plan>:nth-child(2){
    font-size: 1.2rem;
    font-weight: bold;
}

.plan>:nth-child(3){
font-size: 3rem;
font-weight: bold;
}
.plan>:nth-child(5){
font-size: 0.8rem;
}

.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    
}
.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.feature>img{
    width: 1rem;
}
.plans>:nth-child(2)>svg{
fill: white;
}
.plans>:nth-child(2)>button{
    color: orange;
}
